import {
  ActionConfig,
  BaseConnectorConfig,
  FileActionType, FileRegex,
  isActionConfigType,
  isBaseConnectorConfigType
} from '@xip-online-data/types';
import {isSambaConfig, SambaConfig} from "@xip-online-data/samba-client";

export interface SambaSourceConfig {
  directory: string;
  fileSelector?: FileRegex;
  recursive?: boolean;
  action: FileActionType;
  sambaIdentifier: string;
  interval: number;
  optionalHeaders?: string[];
  indexes?: string[];
  processedDirectory?: string;
}

export function isSambaSourceConfig(obj: SambaSourceConfig): obj is SambaSourceConfig {
  return (
    typeof obj.directory === 'string' &&
      (
        obj.action === FileActionType.ACTION_DELETE ||
        obj.action === FileActionType.ACTION_MOVE ||
        obj.action === FileActionType.ACTION_NOTHING
      ) &&
      typeof obj.sambaIdentifier === 'string' &&
      typeof obj.interval === 'number' &&
      (obj.optionalHeaders === undefined || Array.isArray(obj.optionalHeaders)) &&
      (obj.indexes === undefined || Array.isArray(obj.indexes)) &&
      (obj.processedDirectory === undefined || typeof obj.processedDirectory === 'string')
  );
}

export interface SambaSourceConnectorConfig {
  action: ActionConfig;
  samba: SambaConfig;
  directories: SambaSourceConfig[];
}

export function isSambaSourceConnectorConfigType(obj: SambaSourceConnectorConfig): obj is SambaSourceConnectorConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    isActionConfigType(obj.action) &&
    isSambaConfig(obj.samba) &&
    Array.isArray(obj.directories) &&
    obj.directories.every(isSambaSourceConfig)
  );
}
